@import url("https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700&family=Montserrat:wght@700&display=swap");

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: Montserrat;
  /* height: 100vh;
  width: 100vw; */
  /* display: flex;
  flex-direction: column; */
  /* background: #000;
    color: aliceblue; */
}

body {
  height: 100vh;
  width: 100vw;
  display: flex;
  /* flex-direction: column; */
  align-items: center;
  justify-content: center;
}

h1 {
  font-size: 36px;
  font-weight: 700;
  line-height: 44px;
  letter-spacing: 0em;
  text-align: center;
}

button {
  outline: none;
  height: 3rem;
  font-family: Montserrat;
  font-size: 24px;
  font-weight: 700;
  line-height: 29px;
  letter-spacing: 0em;
  text-align: center;
  width: 3rem;
  margin: 0.2rem;
  border: 1px #2b2b2b;
  background: #2b2b2b;
  color: #fff;
}

p {
  font-family: Montserrat;
  font-size: 20px;
  font-weight: 700;
  line-height: 24px;
  letter-spacing: 0em;
  text-align: center;
  margin: -0.2rem ;
  min-width: 100%;
}

input {
  border: 2px solid #2b2b2b;
  width: 100%;
  height: 2.5rem;
  margin: 2% 0rem;
  padding-left: 0.7rem;
}

#fields {
  height: 70%;
  width: 70%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  /* border: 1px solid; */
  gap: 1rem;
}

#root {
  padding: 2%;
  border-radius: 4%;
  height: 60%;
  width: 30%;
  /* border: 1px solid black; */
  box-shadow: -30px 20px 100px 10px #fb184e78, 30px -20px 100px 10px #B8ACE3;
}

#main-box {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
}

#error {
    color: #FB184E;
}

#success {
    color: #0057FF;

}